import { ElementNode, Text, View, hexColor } from '@lightningjs/solid';
import { Column, Row } from '@lightningjs/solid-primitives';
import styles from '../../assets/styles/tiviStyles';
import { For, createSignal, onMount } from 'solid-js';
import { TileRow } from '../components';
import Thumbnail from '../components/Thumnail';
import { dataChannels } from './dataChannels';

const HomePage = () => {
  const [listChannels, setListChannels] = createSignal<any>([]);
  const [columnY, setcolumnY] = createSignal(0);
  function command(jsonString) {
    try {
        let jsonData = window['CrossPlatformBridge'].command(jsonString)
        let object = JSON.parse(jsonData)
        return object
    } catch (e: any) {
        return null
    }
  }

  function ACPB1() {
    let data = {
        "param": {
            "service_no": "A_CPB1",
            "version": "1"
        },
        "payload": {
            "type": "all"
        }
    }

    let channelList = command(JSON.stringify(data))
    if (channelList && channelList.data) {
      return channelList.data
    }
  }

  function A_CPB4(channel_id) {
    let data = {
        "param": {
            "service_no": "A_CPB4",
            "version": "1"
        },
        "payload": {
            "channel_id": channel_id
        }
    }
    let channelList = command(JSON.stringify(data));
    return channelList;
  }
  onMount(async () => {
    try {
      const data = await ACPB1();
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
            JSON.stringify(data) + "&field_2=" + "" + "&field_3=" + ""
      );
      if(data && data.length > 0) {
        let result: any[][] = [];
        for (let i = 0, j = data.length; i < j; i += 6) {
          result.push(data.slice(i, i + 6));
        }
        setListChannels(result);
      }
    } catch (e) {
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
        JSON.stringify(e) + "&field_2=" + "" + "&field_3=" + ""
      );
    }
  });

  const focusItem = (e) => {
    console.log('focusItem', e);
  };

  const openTivi = async (id) => {
    try {
      const data = await A_CPB4(id);
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
            JSON.stringify(data) + "&field_2=" + "" + "&field_3=" + ""
      );
    } catch (e) {
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
        JSON.stringify(e) + "&field_2=" + "" + "&field_3=" + ""
      );
      console.log('error', e);
    }
  }

  function onRowFocus(this: ElementNode) {
    this.children.selected?.setFocus();
    setcolumnY((this.y || 0) * -1 + 24);
  }

  return (
    <View style={styles.TiviWrapper} clipping autofocus>
      <Column plinko autofocus animate style={styles.Container} y={columnY()}>
        <For each={listChannels()}>
          {(items) => (
            <Row style={styles.Row} onFocus={onRowFocus}>
              <For each={items}>
                {(item) => (
                  <View style={styles.Item} onFocus={focusItem} channel-id={item.id} animate onEnter={() => openTivi(item.id)}>
                    <Text style={{ color: hexColor("#ffffff"), fontSize: 32 }}>{item.id.toString()}</Text>
                    <Text style={{ color: hexColor("#ffffff"), fontSize: 32, fontFamily: 'NotoSansJP' }}>{item.channel_name}</Text>
                  </View>
                )}
              </For>
            </Row>
          )}
        </For>
      </Column>
    </View>
  );
};

export default HomePage;
